body {
	background-color: #282c34;
	color: white;
}

area {
	display: block;
}

.App {
  display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.container {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.canvas {
	position: relative;
	display: grid;
	background-image: url('./img/find.png');
	row-gap: 0;
	height: 800px;
	width: 900px;
}

.highscores {
	display: flex;
	flex-direction: column;
	padding-left: 0px;
	align-items: center;
	color: white;
}

.horizontal-container {
	display: flex;
	column-gap: 50px;
	justify-content: space-evenly;
}

.timer {
	display: flex;
	justify-content: space-between;
	column-gap: 15px;
	align-items: baseline;
}

.scores-title {
	display: flex;
	justify-content: center;
	outline: 1px solid rgb(255, 255, 255);
	width: 100%;
	background-color: cornflowerblue;
}

.score {
	display: flex;
	column-gap: 15px;
	outline: 1px solid rgb(255, 255, 255);
	justify-content: space-between;
	padding: 5px;
}

.slot {
	padding: 0px;
	margin: 0px;
	height: 10px;
	width: 10px;
}

.square {
	position:absolute;
  transform:translate(-50%,-50%);
  height:70px;
  width:70px;
  border:5px solid rgb(0, 167, 64);
	pointer-events: none;
	display: none;
}

.test {
	position:absolute;
  width: 75px;
	height: 75px;
	border:5px solid rgb(78, 0, 180);
	border-radius: 50px;
	transform:translate(-50%,-50%);
	display: none;
}

#test {
	left: 252px;
	top: 116px;
}

#a-marker {
	left: 336px;
	top: 632px;
}

.img-div {
	height: 80px;
	width: 100px;
	background-color: #61dafb;
}

.selected {
	border: solid 5px cadetblue;
}

.timer {
	color: white;
}